
.newKeepContainer {
  height: 100%;
  width: 100%;
  z-index: 3;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.form-modal {
  color: azure;
  position: relative;
  width: 50%;
  height: 50%;
  background: #2c3e50;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.8);
  margin-top: 25%;
  margin-left: 25%;
  border-radius: 10px;
}



